@import '$assets/scss/main.scss';
.f_featureWrapper2 {
    width: 200px;
    background-color: #191919;
    overflow: hidden;
    text-overflow: ellipsis;
    border-radius: 8px;
    box-shadow: 4px -8px 2px 0px rgba(0, 0, 0, 0.4);
    // margin: 2px;

    .title {
        width: 200px;
    }



    @media screen and (max-width: 576px) {
        width: 145px;

        .title {
            width: 145px;
        }
    }
}

.f_featureWrapper {
    // min-height: 185px;
    // min-width: 300px;
    // max-width: 450px;
    // height: 100%;
    // margin-left: 1rem;
    // margin-bottom: 1rem;
    background-color: #191919;
    // box-shadow: 3px 3px 3px #111;
    position: relative;
    // border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-self: stretch;
    aspect-ratio: 1/1;
    height: 200px;

    @media screen and (max-width: 576px) {
        height: 145px;
    }
}

.f_featureWrapperVideo3 {
    overflow: hidden;
    text-overflow: ellipsis;
    width: 275px;
    background-color: #191919;
    border-radius: 8px;
    box-shadow: 4px -8px 2px 0px rgba(0, 0, 0, 0.4);

    @media only screen and (max-width: 768px) {
        width: 346px;
        
    }
}

.f_featureWrapperVideo {
    // min-height: 185px;
    // min-width: 300px;
    // max-width: 450px;
    // height: 100%;
    // margin-left: 1rem;
    // margin-bottom: 1rem;
    background-color: #191919;
    // box-shadow: 3px 3px 3px #111;
    // aspect-ratio: 16/9;
    position: relative;
    // border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-self: stretch;
    height: 180px;

    @media only screen and (max-width: 768px) {
        height: 150px;
        
    }
}

.f_featureWrapperMovie {
    // min-height: 185px;
    // min-width: 300px;
    // max-width: 450px;
    // height: 100%;
    // margin-left: 1rem;
    // margin-bottom: 1rem;
    background-color: #191919;
    // box-shadow: 3px 3px 3px #111;
    aspect-ratio: 27/40;
    position: relative;
    // border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-self: stretch;
    height: 250px;

}

.content {
    opacity: 0;
    transition: all 0.3s ease-in;

    @media only screen and (max-width: 676px) {
        opacity: 1;
    }
}

.content.active {
    opacity: 1;
    transition: all 0.3s ease-out;
}

