.f_featureContentWrapper {
    height: 40%;
    width: 100%;
    bottom: 0;
}

.f_featureActionBtn {
    height: 30px;
}

.f_featureSummary {
    color: $white;
}

.f_featurePane {
    // position: absolute;
    z-index: $z-index-lift;
    padding: 10px;
}

.f_featurePlayBtn {
    background-color: transparent;
    border: none;
    &:focus {
        outline: 0;
    }
    position: absolute;
    right: 0;
    bottom: 5px;
}

.f_featureHeaderWrapper {
    position: absolute;
    width: auto;
    top: 0px;
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
    // border-radius: 10px;
}

.f_featureHeaderWrapperTitle {
    background-color: rgba(0, 0, 0, 0.7);
    color: $white;
    display: inline-block;
    font-size: 6px;
}

.f_defaultFeatureAvatar {
    height: 50px;
    width: 50px;
    margin-right: 10px;
}

.f_realFeatureAvater {
    height: 50px !important;
    width: 50px !important;
    border-radius: 50% !important;
}

.f_menuIcon {
    width: 15px;
}

.f_description {
    font-size: 12px;
    max-width: 170px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #884090;

    &:hover {
        cursor: pointer;
    }
}

.f_bottom_icon {
    height: 12px;
    color: white;
    margin-left: 10px;
    float: right;
}

.f_fontSize10 {
    font-size: 10px;
}

.f_fontSize12 {
    font-size: 12px;
}

.f_hoverCursor_icon {
    color: #2B7676;
}

.f_hoverCursor_icon:hover {
    cursor: pointer;
}

.f_fontSize16 {
    font-size: 16px;
    font-size: 1.1rem;
    line-height: 1.2rem;
    max-height: 2.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 0.5rem;
}

.f_hoverCursor {
    color: #2B7676;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 10px;
}

.f_hoverCursor:hover {
    cursor: pointer;
}

.views {
    font-size: 8px;
    color: white;
}

.viewallcomments {
    cursor: pointer;
}

.likeandcomment {    
    display: flex;

    position: absolute;
    width: auto;
    bottom: 0px;
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
    // border-radius: 10px 10px 0 0;

    padding: 5px 5px;
    align-items: baseline;
    // border-radius: 0 0 10px 10px;
}

.no_of_likes {
    margin-left: auto;
    font-size: 8px;
    color: white;
}

.title {
    font-size: 12px;
    padding:3px;
    cursor: pointer;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-weight: bold;

    &::hover {
        color: #884090;
    }
}

.rowBg {
    background-color: rgba(0, 0, 0, 0.75);
}